@charset "UTF-8";
html {
  font-family: Helvetica Neue, Helvetica,'微软雅黑',' \5FAE\8F6F\96C5\9ED1', Arial, sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 62.5%;
  line-height: 1.5;
  background-color: #fff; }

body {
  padding: 0;
  margin: 0;
  font-size: 26px;
  -webkit-overflow-scrolling: touch; }

* {
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background: transparent;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  transition: all 0.25s ease 0s; }
  a:active {
    outline: 0; }

b, strong {
  font-weight: bold; }

mark {
  background: #ff0;
  color: #000000; }

small {
  font-size: 80%; }

img {
  border: 0;
  vertical-align: middle;
  width: 100%; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word; }

button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
  border: none;
  outline: none;
  -webkit-appearance: none; }

button {
  overflow: visible; }

button, select {
  text-transform: none; }

button, html input {
  cursor: pointer; }

button[disabled], html input[disabled] {
  cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  line-height: normal;
  box-sizing: border-box;
  padding: 0; }

input::-webkit-outer-spin-button {
  height: auto;
  -webkit-appearance: none; }

fieldset {
  border: 0.083333rem solid #c0c0c0; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto;
  resize: vertical; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

ul, li, ol {
  list-style: none outside none; }

h1, h2, h3 {
  font-weight: normal;
  font-size: 100%; }

input::-moz-placeholder, textarea::-moz-placeholder {
  color: #cccccc; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #cccccc; }

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #cccccc; }

input[type="button"] {
  -webkit-appearance: none;
  appearance: none; }
